import { Block, BlockChildren } from '@ali/4ever-cangjie';
import type { ParagraphProperties, ParagraphData, ParagraphChildren } from '@ali/4ever-plugin-paragraph';
import { FoldableHeadingData } from './heading1';

export default class Heading2 extends Block<FoldableHeadingData, ParagraphChildren> {
  static TYPE = 'heading-2';

  static create<SD = ParagraphData, SN extends BlockChildren = ParagraphChildren>(
    properties: Heading2 | ParagraphProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Heading2.create` does not accept string');
    }
    if (Heading2.isHeading2(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Heading2.TYPE,
    });
  }

  static isHeading2(maybeHeading2: any): maybeHeading2 is Heading2 {
    return Block.isBlock(maybeHeading2) && maybeHeading2.type === Heading2.TYPE;
  }

}
